import React from 'react';
import {Collapse} from 'antd';
import ArchiveList from './ArchiveList'


const Panel = Collapse.Panel;

const styles={
  monthStyle:{
    background: '#f0f0f0',
    borderRadius: 4,
    marginBottom: 24,
    border: 0,
    overflow: 'hidden',
  }
}

export default class MonthCollapse extends React.Component {

  constructor(props) {
    super(props)
    const {activePanel} = props
    this.state = {
      curActivePanel: activePanel
    }
  }

  // 
  shouldComponentUpdate(nextProps,nextState){
    const {month,activePanel}=nextProps
    const {curActivePanel}=nextState
    return !activePanel.includes(month) && !curActivePanel.includes(month)
  }

  componentDidUpdate(){
    if(this.props.activePanel[1]!==this.state.curActivePanel[1]){
      // 
      // setTimeout(()=>{
        this.setState({
          curActivePanel:this.props.activePanel
        })
      // },200)
    }
  }
  render() {
    const {dayList,activePanel,changeActiveMonth,month,archiveEachPage}=this.props
    return (
        <Collapse accordion
                  activeKey={activePanel[1]}
                  onChange={changeActiveMonth}
        >
                <Panel header={month+""} key={month+""} style={styles.monthStyle}>
                  <ArchiveList dayList={dayList} archiveEachPage={archiveEachPage}/>
                </Panel>

        </Collapse>
    )
  }
}

